.jobAdsDetailsWrapper{
    color: #666;
}
.jobAdsDetailsWrapper .fullRow{
    width: 100%;
    float: left;
}
.jobAdsDetailsWrapper .locationDurationContainer .locationBox{
    margin-right: 20px;
}
.jobAdsDetailsWrapper .locationDurationContainer .locationValue,
.jobAdsDetailsWrapper .locationDurationContainer .durationValue{
    display: inline-block;
    margin-left: 10px;
    position: relative;
    top: -5px;
}
.jobAdsDetailsWrapper .applyBtnWrapper{
    margin: 15px 0px;
}
.jobAdsDetailsWrapper .applyBtnWrapper .applyBtn{
    margin-right: 50px;
}
.jobAdsDetailsWrapper .contactPersonInfo{
    margin-bottom: 20px;
}
.jobAdsDetailsWrapper .socialIconsWrapper .social-icon{
    width: 40px;
    height: 40px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border-radius: 50%;
    text-indent: -999em;
    overflow: hidden;
    margin-left: 10px;
    float: left;
    background-repeat: no-repeat;
    background-position: center center;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    fill: #fff;
    border: 2px solid transparent;
    transition: all .1s ease;
}
.jobAdsDetailsWrapper .socialIconsWrapper .social-icon > svg{
    transition: none;
}
.jobAdsDetailsWrapper .socialIconsWrapper .twitter-icon{
    background-color: #27A0DD;
}
.jobAdsDetailsWrapper .socialIconsWrapper .twitter-icon:hover{
    background-color: #fff;
    border-color: #27A0DD;
    fill: #27A0DD;
}
.jobAdsDetailsWrapper .socialIconsWrapper .facebook-icon{
    background-color: #335290;
}
.jobAdsDetailsWrapper .socialIconsWrapper .facebook-icon:hover{
    background-color: #fff;
    border-color: #335290;
    fill: #335290;
}
.jobAdsDetailsWrapper .socialIconsWrapper .linkedin-icon{
    background-color: #2164A9;
}
.jobAdsDetailsWrapper .socialIconsWrapper .linkedin-icon:hover{
    background-color: #fff;
    border-color: #2164A9;
    fill: #2164A9;
}